/**
 * hydrate the page to make sure both server and client
 * side pages are identical. This includes markup checking,
 * react comments to identify elements and more.
 */
const elRoot = document.getElementById('app');
const renderDOM = process.env.NODE_ENV=='production'? hydrate: render;
const renderD = (Component) => {
    // eslint-disable-next-line no-undef
    console.log(window._INIT_STATE_, create(window._INIT_STATE_))
    renderDOM(<Provider store={create(window._INIT_STATE_)}>
            <Component/>
        </Provider>, elRoot
    )
};


